@use "global" as *;


.footer {
  background: $color-background-base-2;
  padding-top: rem(40.72);
  padding-bottom: rem(41.49);
}

.footer__inner {

}

.footer__wrap {

}

.footer__top {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: rem(19.63);
}

.footer__title {

}

.footer__info {

}

.footer__sns-sp {

}

.footer__nav {
  margin-top: rem(20.74);
}

.footer__bottom {
  margin-top: rem(24.2);  // (56.2 - 32) カンプの間隔 - 『nav__item』のpadding
  display: flex;
  justify-content: space-between;
  align-items: flex-start;;
}

.footer__bottom-left {

}

.footer__bottom-text {
  color: $color-white;
  leading-trim: both;
  text-edge: cap;
  font-size: rem(12);
  line-height: 1.6;
}

.footer__google-policy {
  border-bottom: 1px solid $color-white;
  transition: opacity 0.2s ease-out;
}

.footer__google-policy:hover {  // hover
  opacity: 0.7;
}

.footer__terms {
  border-bottom: 1px solid $color-white;
  transition: opacity 0.2s ease-out;
}

.footer__terms:hover {  // hover
  opacity: 0.7;
}

.footer__copyright {
  display: block;
  margin-top: rem(40);
  margin-left: rem(11.6);

  color: $color-white;
  text-align: center;
  leading-trim: both;
  text-edge: cap;
  font-family: $font-family-en-1;
  font-size: rem(16);
  letter-spacing: 0.04em;
  text-indent: 0.04em;
  text-transform: capitalize;
}

.footer__bottom-right {

}

.footer__sns-pc {

}

.footer__policy {
  display: block;
  width: fit-content;
  margin-left: auto;
  margin-top: rem(37.47);

  color: $color-white;
  leading-trim: both;
  text-edge: cap;
  font-size: rem(12);
  line-height: 1.6;
  text-decoration-line: underline;
  text-decoration-style: solid;
  text-decoration-skip-ink: none;
  text-decoration-thickness: auto;
  text-underline-offset: auto;
  text-underline-position: from-font;
  transition: opacity 0.2s ease-out;
}

.footer__policy:hover {  // hover
  opacity: 0.7;
}

.footer__top-btn {

}
